export function bottomSheetUtils() {
    let isDragging = false;
    const bottomSheet = document.getElementById('bottomSheet');
    let dragElement = document.getElementById('bottomSheet');
    let initialY = 0;
    let currentY = 0;
    let targetY = 523; // ëª©í‘œ ìœ„ì¹˜ (ì˜ˆ: 200px)
    let isReturning = false; // ì¶”ê°€ëœ ë³€ìˆ˜: ì›ë˜ ìœ„ì¹˜ë¡œ ëŒì•„ê°€ëŠ” ì¤‘ì¸ì§€ ì—¬ë¶€
  
    dragElement.addEventListener('mousedown', startDrag);
    dragElement.addEventListener('mousemove', drag);
    dragElement.addEventListener('mouseup', stopDrag);
  
    function startDrag(event) {
      isDragging = true;
      if (!isReturning) { // ì›ë˜ ìœ„ì¹˜ë¡œ ëŒì•„ê°€ëŠ” ì¤‘ì´ ì•„ë‹ ë•Œë§Œ ì´ˆê¸°í™”
        initialY = event.clientY;
        currentY = event.clientY - initialY;
      }
      console.log(event.clientY);
    }
  
    function drag(event) {
      if (isDragging) {
        if (!isReturning) { // ì›ë˜ ìœ„ì¹˜ë¡œ ëŒì•„ê°€ëŠ” ì¤‘ì´ ì•„ë‹ ë•Œë§Œ ë“œë˜ê·¸ ì ìš©
          currentY = event.clientY - initialY;
          if (currentY < targetY) {
            dragElement.style.transform = `translateY(${currentY}px)`;
          } else {
            dragElement.style.transform = `translateY(${targetY}px)`;
            stopDrag();
          }
        }
      }
    }
  
    function stopDrag() {
      isDragging = false;
      if (currentY >= targetY) {
        isReturning = true;
        dragElement.style.transform = 'translateY(0)';
      } else {
        initialY = currentY;
      }
      console.log(initialY, 'stop drag initial Y');
    }
  }
  